.bin-container{
    width:100%;
    min-height: 65vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    .empty-bin-msg{
        width:300px;
        height:100px;
        display: flex;
        justify-content:center;
        align-items:center;
        font-size: 18px;
    }
}

.bin-item{
    border: 1px solid darkslategray;
    border-radius: 5px;
    padding:5px;
    margin: 5px;
    width: 60%;
    
    color: black;
    display: flex;
    img{
        width:150px;
    }
    .bin-item_info{
        display: flex;
        flex-direction: column;
        justify-content: center;
        color: grey;
        *{
            margin: 10px;
        }
        b{
            color: orange;
        }
        div{
            button{
                background-color: darkslategray;
                padding:8px;
                color:white;
                border-radius: 5px;
            }
        }
    }

}















/* Smartphones (portrait and landscape) ----------- */
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {

    .bin-container{
        flex-direction: column;
    }

    .bin-item{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        img{
            width: 100px;
        }

    }

    .bin-item_info{
        *{
            margin: 5px;
            font-size: 11px;
        }
        
        div{
            display: flex;
            justify-content: space-between;
            button{
                background-color: darkslategray;
                padding:5px;
                color:white;
            }
        }
    }


