$mainColor:darkslategray;

.profile-page{
    min-height:70vh;
    .container{
        display:flex;
        justify-content:space-around;
        padding:20px 0px;
    }
}


.profile-library{
    width:75%;
}

.library-title{
    width:100%;
    text-align: center;
    padding: 15px;
    background-color: $mainColor;
    h1{
        color:white;
        font-size: 20px;
    }

}

.library-books_wrapper{
    padding: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px;
}

.library-book_card{
    width:300px;
    height:300px;
    padding: 5px;
    display:flex;
    flex-direction: column;
    align-items: center;
    background-color: whitesmoke;
    border-radius: 5px;
    border: 1px solid $mainColor;
    img{
        height: 60%;
    }
    h2{
        width: 100%;
        height: 15%;
        display: flex;
        align-items: center;
        justify-content: center;
        color:$mainColor;
        text-align:center;
    }
}

.btns-wrap{
    height: 25%;
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    button{
        background-color: $mainColor;
        color:white;
        padding:5px;
    }
}

