.book-btn{
    width: 50%;
    margin-top: 10px;
    color:white;
    font-size: 14px;
    text-align: center;
    padding: 5px 10px;
    border-radius: 5px;
    background-color: darkslategray;
}

.disabled{
    background:#54ff79;
    color:white;
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {
    .book-btn{
        width:60%;
    }
}