.profile-pop_background{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10;
    background-color: rgba(0,0,0,0.8);

    width:100vw;
    height: 100vh;   
}

.profile-pop_container{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content:center;
    align-items:center;
}

.profile-pop{
    min-width:60vw;
    max-height:500px;
    padding:10px;
    background-color: white;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow:scroll;
}

.close-btn_wrapper{
    position: absolute;
    top: -25px;
    right: 100px;
}

.close-btn{
    
    position: fixed;
    width: 100px;
    color: white;
    font-size: 18px;
    background: orangered;
    border-top-left-radius: 10px;
    padding: 2px 0px;

}


.profile-content_wrapper {
    width:100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin: 5px 0px;
    border-radius: 5px;
}

.profile-titles_wrapper{
    border-bottom: 1px solid darkslategray;
    color:darkslategray;
}

.profile-titles_wrapper h1{
    padding:20px;
    text-align: center;
    line-height:30px;
    font-size: 12px;
    
}

.profile-titles_wrapper h1 b{
    font-size: 20px;
    color:black;
}

.profile-titles_wrapper h2{
    width: 300px;
    text-align:justify;
    padding:10px 0px;
}

.profile-btns_wrapper{
    display: flex;
    flex-direction:column;
    justify-content:space-around;
    align-items: center;
    padding:20px 0px;
   
}

.profile-logIn_btn, .profile-register_btn{
    background-color: darkslategray;
    color: white;
    width: 300px;
    height: 30px;
    margin: 5px 0px;
    font-size: 20px;
}

