$mainColor:darkslategray;

.page{
  background-color: whitesmoke;
    margin: 0 auto;
    width: 100vw;
    color: darkslategray;
  }

  .page-body{
    background-color: white;
    display: grid;
  
     grid-template-columns: 20% 80%;
     grid-template-rows: 1fr 1fr;
  
     gap: 0px;
  }
  
  
  
  .page-aside{
  
    grid-row-start: 1;
    grid-row-end: 1;
  
    grid-column-start: 1;
    grid-column-end: 1;
    text-align: center;
    min-height: 400px;
    padding: 30px 0px;
  }
  
  
  
  .comments{
  
    grid-row-start: 2;
    grid-row-end: 2;
  
    grid-column-start: 1;
    grid-column-end: 3;
  }
  

  
  .btn{
    margin-top: 10px;
    border: none;
    background: darkslategray;
    border: 1px solid darkslategray;
    color: white;
    width: 50%;
    padding: 5px 10px;
    border-radius: 5px;
  
  }
  
  .book-info{
    display: flex;
    flex-direction: column;
  }
  
  .book-title{
    padding: 10px;
  }
  
  .book-publisher{
    padding: 10px;
  }
  
  .page-main{
    border-left: 5px solid whitesmoke;
  
     grid-row-start: 1;
     grid-row-end: 2;
  
     grid-column-start: 2;
     grid-column-end: 2;
     padding: 30px;
  }


  
  .book-descr{
    border-top:1px solid whitesmoke;
    padding: 20px;
    h1{
      text-align:center;
      background-color: $mainColor;
      color:white;
      padding: 10px 0px;
      margin-bottom:10px;
    }
  }
  
  


  
  .input-holder{
    border-top: 1px solid whitesmoke;
    padding: 10px 10px;
    display: flex;
    flex-direction: column;
    h2{
      padding: 10px;
    }
    button{
      width:100px;
      margin-left: 25px;
    }
  }
  
  
  .comment-inp{
    margin-left: 25px;
    background-color: whitesmoke;
    border: 1px solid dimgrey;
    border-radius: 5px;
    width:200px;
  }
  
  .comments-holder{
    padding: 10px 10px;
    border-top: 1px solid whitesmoke;
    h2{
      padding: 10px;
    }
    div{
      margin: 5px 0px;
    }
  }
  
  .comment{
    display: flex;
    border: 1px solid gray;
    margin-left: 25px;
  }
  
  .users-name{
    padding: 10px;
    display: flex;
    justify-content: center;
    text-align: right;
  }
  
  .users-comment{
    font-style: italic;
    padding: 10px;
    border-left: 5px solid whitesmoke;
  }
 
  
  // ------------------------------------------------------------------
  @media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {

  .page-body{
    background-color: white;
    display: flex;
    flex-direction: column;
  }

  .page-main{
     padding: 0px;
  }

  .book-descr{
    padding: 10px;
    font-size: 12px;
  }

  .comment-inp{
    margin-left: 10px;
    width: 90vw;
  }

  .comment{
    display: flex;
    flex-direction: column;
    margin: 5px;
  }

  .users-comment{
   font-size: 11px;
  }

  .input-holder{
    button{
      align-self: end;
    }
  }
}
