.slick-slider{
    width: 100%;
    height: 250px;
    margin: 10px auto;
    position: relative; 
    box-shadow:0 0 10px black;
    padding: 10px;

}


.slick-track{
    display: flex;
    height: 100%;
    align-items: center;   
}

.slick-list{
    overflow:hidden;
    height:100%;
    padding:0px 10px;
    padding: 0px 100px;
    
}

.slick-slide{
    height: 20vh;
}

.slide{
    /* width: 500px; */
    height: 20vh; 
    padding: 0px 5px;
}



.slick-prev{
    position: absolute;
    left: 0;
    top:50%;
    margin-top: -15px;
    z-index: 3;
    font-size: 0;
    background:url('../png/leftA.png') 0 0 / 100% no-repeat;
    background-size: contain;
    width: 30px;
    height: 30px;
    border-radius: 50%;
}
.slick-next{
    position: absolute;
    right: 0;
    top:50%;
    margin-top: -15px;
    font-size: 0;
    background:url('../png/rightA.png') 0 0 / 100% no-repeat;
    background-size: contain;
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

.slide-content{
    display: flex;
    width: 350px;
    height: 100%;
    border-radius: 5px;
    overflow:hidden ;
    border: 1px solid darkslategray;
    background-color: white;
    
}

.slide-img{
    height: 100%;
}

.content-info_wrap{
    padding: 5px 10px;
}

.content-name{
    color: darkslategray;
    height: 50%;
    font-size: 16px;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 5px 10px;
    border-bottom:1px solid darkslategray
}

.content-year{
    color: darkslategray;
    font-size: 11px;
    margin: 10px ;
    text-align: center;
    padding: 5px 10px;
}

.content-publisher{
    color: darkslategray;
    font-size: 11px;
    text-align: center;
    margin: 10px ;
    padding: 5px 10px;
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {
/* Styles */

.slick-slider{
    height: 150px;
}

.slide-img{
    width: 120px;
}

.slick-prev,.slick-next{
    z-index: -10;
}

.content-info_wrap{
    width: 100%;
}

.slide-content{
    width: 95vw;   
}

.content-name{
    border:none;
    padding: 0px;
}

.content-year, .content-publisher{
    padding: 0px;
}




}
