.main{
    width: 100vw;
    background-color: whitesmoke;
}
// .m-container{
// }

.m-sections_wrapper{
    display: flex;
    justify-content: space-between;
    box-shadow:0 0 10px black;
}

.m-categories{
    min-width: 15%;
}

.m-categories_wrapper{
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    background-color: white;
    color: darkslategray;
    
}


.m-categories_wrapper h2{
    background-color: darkslategray;
    color: white;
    text-align: center;
    padding: 10px 0px;
}


.m-category-li_wrapper{
    text-align: left;
    padding: 5px 5px;
    font-size: 12px;
}

.m-category_li{
    border-top: 1px solid darkslategray;
    padding: 5px 10px 5px 5px;
}

.m-category_li:hover{
    color: white;
    background-color:darkslategray;
    text-align: right;
    cursor: pointer;
}

.m-body{
    width: 80%;
    background-color: white;
    
}

.m-blanks_wrapper{
    display: flex;
    justify-content: end;
    background-color: whitesmoke;
}

.m-blank{
    padding: 1%;
    color: dimgrey;
    display: flex;
    align-items: center;
    cursor:pointer;
    background-color: white;
}

.active-blank{
    background-color: darkslategray;
    color: white;
}

.m-blank p{
    cursor: default;
}

.m-blank b{
    background-color: darkslategray;
    color: white;
    padding: 3px 5px;
    margin-left: 5px;
    border-radius:5px;
    cursor:pointer;

}


.m-hidden_btns,.hidden-sort-btn, .hidden-pick-publisher-btn{
display: none;
}





/* Smartphones (portrait and landscape) ----------- */
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {
/* Styles */

.m-body{
    width: 100%; 
}

.m-categories{
    position: relative;
    min-width: 0;
    display: none;
}

.m-categories_wrapper{
    position: absolute;
    left: 15px;
    top: 95px;
    width: 95vw;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content:center;
    background-color: white;
    border-top: 1px solid darkslategray;
    
}

.m-categories_wrapper h2{
  display: none;
}

.m-category-li_wrapper{
    width: 100%;
    display: flex;
    justify-content:space-between;
    align-items: center;
}

.m-category_li{
    border: none;
    padding:5px;
}








.m-blanks_wrapper{
justify-content: center;
height:80px;
border: 1px solid darkslategray; 
background-color: white;
display: none;  
}

.m-blank{
    height: 40px;
    padding: 10px 1px;
    margin: 3px;
    color: darkslategray;
    display: block;
    align-items: center;
    cursor:pointer;
    background-color: transparent;
    padding: 1%;
    color: dimgrey;
    display: flex;
    align-items: center;
    cursor:pointer;
    background-color: white;
}

.m-hidden_btns{
    width:100vw;
    height:50px;
    display: flex;
    justify-content:space-around;
    align-items:center;
    color:darkslategray;
}

.hidden-sort-btn, .hidden-pick-publisher-btn{
width:200px;
height:100%;
text-decoration: underline;
display: flex;
align-items: center;
justify-content:center;
}

.hidden-sort-btn::before{
    content: "\2959";
}
.hidden-pick-publisher-btn::after{
    content: "\2955";
}

.active-blank{
    background-color: darkslategray;
    color: white;
}

}
