a{
    list-style: none;
    color: white;
}


.pop-up{
    min-width: 30vw;
    height: 261px;
    position: absolute;
    background-color: darkslategray;
    z-index: 10;
    top: 5vh;
    padding: 0 20px;
    margin-top: -2px;
    display: none;
    li{
        margin-top: 5px;
        padding: 10px 5px;
        border-bottom: 1px solid white;
    }
}


/* Smartphones (portrait and landscape) ----------- */
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {
    .pop-up{
        min-width:80px;
        height: 160px;
        li{
            
            font-size:11px;
        }
    }

}

