.m-book_card{
    padding:10px 0px;
    text-align: center;
    border: 1px solid darkslategray;
    border-radius: 5px;
    min-height: 500px;
    box-shadow:0 0 10px black;

}

.book-img{
width: 100px;
height: 150px;
}

.book-name{
    padding: 10px 5px;
    font-size: 14px;
    color: dimgrey;
    min-height: 10vh;
}
.book-info{
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        font-size: 12px;
        padding: 10px 0px;
}

.book-info p{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
}

.book-info span{
    width: 80px;
    text-align: right;
    color: olive;
    font-size: 10px;
    margin: 5px 5px 0px 5px;
}

.book-info b{
    width: 80px;
    text-align: left;
    font-weight: bold;
    margin: 5px 5px 0px 5px;

}
.book-price{
    margin-top: 10px;
    color:white;
    font-size: 24px;
    text-align: center;
    border: 1px solid white;
    background-color: darkslategray;
}

.book-rating{
    color: orange;
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {
/* Styles */

.m-book_card{
    padding:5px 0px;
    min-height: 420px;
}

.book-img{
    width: 140px;
    height: 190px;
    border-radius: 5px;
    }


    .book-name{
        margin: 2px 5px;
        font-size: 13px;
        color: dimgrey;
        display: flex;
        justify-content: center;
        align-items:center;
    }
    .book-info{
            padding: 5px 0px;
    }
    
    .book-info p{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr;
    }

    .book-price{
        margin: 0px;
    }


}