.header{
    width: 100vw;
    background-color: darkslategray;
    color:white;
}



.h-blocks_wrapper{
    height: 100%;
    display:flex;
    align-items: center;
    justify-content: space-around;
}

.h-logo_block{
    background: url('../png/book-logo_w.png');
    width:150px;
    height:150px;
    background-size:cover;

}

.h-inputs_holder{
    height:3vh;
    padding: 3px 5px;
    border-radius: 5px;
    display:flex;
    justify-content: space-between;
    align-items: center;
}
.h-search_block{
    width:30%;
}

.h-search_inp{
    width: 96%;
    height:30px;
    margin-right: 5px;
    border: none;
    border-radius: 3px;
}

.h-btns_container{
    width: 150px;
    display: flex;
    justify-content: space-around;
}

.h-profile_block{
    font-size:30px;
}

.h-bin_block{
    display:flex;
    justify-content:center;
    align-items: center;
}

.fa-shopping-cart{
    font-size: 30px;
    margin-right: 5px;
}

.h-bin-count_disp{
    background-color: orange;
    padding: 0px 5px;
    border-radius: 50%;
    margin-top: -30px;
    margin-left: -5px;
}


.h-navigation_wrapper{
    height: 5vh;
    display: flex;
    justify-content: space-around;
    border-top: 1px solid white;
}

.h-navigation_item{
    position: relative;
    display: flex;
    top: 0;
}

.nav-link{
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    padding: 0px 10px;
    min-width: 30vw;
}

.nav-link:hover{
    background-color: white;
    color: darkslategray;
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {
/* Styles */

.h-blocks_wrapper{
    width: 100%;
    height: 100%;
    display:flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
}

.h-logo_block{
// display: none;
}

.h-inputs_holder{
    height:80px;
}

.h-search_block{
    width:75vw;
}

.h-btns_container{
    width:60%;
    padding:15px;
    display: flex;
    justify-content: space-between;
}


.nav-link{
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    padding: 0px 10px;
    min-width: 100px;
}

}
